.advantages {
  .centering-wrapper(@width-mobile);
  padding-top: 20px;
  padding-bottom: 49px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @media (min-width: @width-tablet) {
    .centering-wrapper(@width-tablet);
    padding-top: 45px;
    padding-bottom: 47px;
  }

  @media (min-width: @width-desktop) {
    .centering-wrapper(@width-desktop);
  }
}

.advantages__title {
  padding-bottom: 20px;

  @media (min-width: @width-tablet) {
    padding-bottom: 32px;
  }

  @media (min-width: @width-desktop) {
    padding-top: 71px;
  }
}

.advantages__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;

  @media (min-width: @width-tablet) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 153px;
    row-gap: 37px;
    counter-reset: section;
  }

  @media (min-width: @width-desktop) {
    grid-template-columns: repeat(5, 1fr);
    column-gap: 80px;
  }
}

.advantages__item {
  margin-top: 20px;
  padding-left: 80px;

  &--icon-leaf {
    background-image: url(../img/icons/stek.svg#icon-leaf);
    background-repeat: no-repeat;
    background-position: -113px center;

    @media (min-width: @width-tablet) {
      background-size: 80px 80px;
    }
  }

  &--icon-glass {
    background-image: url(../img/icons/stek.svg#icon-glass);
    background-repeat: no-repeat;
    background-position: -113px center;

    @media (min-width: @width-tablet) {
      background-size: 80px 80px;
    }
  }

  &--icon-tableware {
    background-image: url(../img/icons/stek.svg#icon-tableware);
    background-repeat: no-repeat;
    background-position: -113px center;

    @media (min-width: @width-tablet) {
      background-size: 80px 80px;
    }
  }

  &--icon-clock {
    background-image: url(../img/icons/stek.svg#icon-clock);
    background-repeat: no-repeat;
    background-position: -113px center;

    @media (min-width: @width-tablet) {
      background-size: 80px 80px;
    }
  }

  @media (min-width: @width-tablet) {
    padding-top: 151px;
    padding-bottom: 57px;
    width: 230px;
    padding-left: 0;
    margin-top: 0;
    position: relative;
    width: 248px;

    &--description {
      position: relative;
      z-index: 2;
    }

    &--icon-leaf {
      grid-area: 1 / 1 / 2 / 2;
      background-position: 0px 48px;
    }

    &--icon-glass {
      grid-area: 1 / 2 / 2 / 3;
      background-position: 0px 48px;
    }

    &--icon-tableware {
      grid-area: 2 / 1 / 3 / 2;
      background-position: 0px 48px;
    }

    &--icon-clock {
      grid-area: 2 / 2 / 3 / 3;
      background-position: 0px 48px;
    }

    &::after {
      position: absolute;
      counter-increment: section;
      content: counters(section,".") " ";
      font-family: "Oswald", "Arial", sans-serif;
      font-weight: 400;
      font-size: 280px;
      color: @basic-grey;
      top:135px;
      right: -5px;
    }
  }

  @media (min-width: @width-desktop) {
    &--icon-leaf {
      grid-area: 1 / 1 / 2 / 2;
    }

    &--icon-glass {
      grid-area: 1 / 2 / 2 / 3;
    }

    &--icon-tableware {
      grid-area: 1 / 3 / 2 / 4;
    }

    &--icon-clock {
      grid-area: 1 / 4 / 2 / 5;
    }
  }
}
